:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.sl-wrapper {
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: brightness(50%);
  backdrop-filter: brightness(50%);
  background-color: rgba(0, 0, 0, .8);
  border: none;
  outline: none;
}

.sl-close, .sl-next, .sl-prev, .sl-counter {
  color: #fff !important;
  text-shadow: 0 0 20px #9f9f9f !important;
}

.sl-close {
  border-radius: 50% !important;
  box-shadow: inset 0 30px 60px -10px rgba(233, 233, 234, .08), inset 0 18px 36px -10px rgba(33, 32, 32, .3) !important;
}

.sl-caption {
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  background: rgba(0, 0, 0, .4) !important;
}

.sl-counter {
  top: 24px !important;
  right: 73px !important;
}

.sl-image {
  box-shadow: 0 1px 60px rgba(141, 141, 141, .2), 0 -12px 30px rgba(0, 0, 0, .12), 0 4px 6px rgba(102, 102, 102, .12), 0 12px 13px rgba(0, 0, 0, .17), 0 -3px 5px rgba(0, 0, 0, .09) !important;
}

